table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
    font-size: 16px;
    font-family: Arial, sans-serif;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
  }
  
  th {
    background-color: #f4f4f4;
    font-weight: bold;
  }
  
  tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  tr:hover {
    background-color: #f1f1f1;
  }

a {
  text-decoration: none;
  color: #0073e6; 
  font-weight: bold; 
  transition: color 0.3s ease, text-shadow 0.3s ease; 
}

a:hover {
  color: #005bb5; 
  text-shadow: 0px 0px 5px rgba(0, 115, 230, 0.6);
}

table a {
  padding: 5px 10px; 
  border-radius: 5px; 
  background-color: #f9f9f9; 
}

table a:hover {
  background-color: #f1f1f1; 
  color: #003f7d; 
  text-shadow: 0px 0px 5px rgba(0, 63, 125, 0.8);
}

code {
  font-family: 'NanumGothic', monospace;
  background-color: #f4f4f4;
  color: #000000;
  font-size: 15px;
  font-weight: 800;
  padding: 2px 4px;
  border-radius: 3px;
  line-height: 2;
  display: inline-block;
}

code:hover {
  background-color: #eaeaea;
}

hr {
  margin: 40px 40px; 
  border: 3px solid #333;
  margin-left: auto; 
  margin-right: auto;
}
